/* =========================================================
   영화 섹션
========================================================= */
.movie-section {
  margin-bottom: 40px;
  position: relative;
  padding: 0 60px; /* 좌우 여백 */
  animation: sectionFadeIn 0.8s ease-out;
}

.movie-section:nth-child(2) { animation-delay: 0.1s; }
.movie-section:nth-child(3) { animation-delay: 0.2s; }
.movie-section:nth-child(4) { animation-delay: 0.3s; }
.movie-section:nth-child(5) { animation-delay: 0.4s; }
.movie-section:nth-child(6) { animation-delay: 0.5s; }

@keyframes sectionFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* =========================================================
   위시리스트 섹션 특별 스타일
========================================================= */
.wishlist-section {
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.08), rgba(255, 215, 0, 0.03));
  border: 2px solid rgba(255, 215, 0, 0.3);
  border-radius: 20px;
  padding: 30px 60px;
  box-shadow: 
    0 10px 40px rgba(255, 215, 0, 0.2),
    inset 0 0 20px rgba(255, 215, 0, 0.05);
  backdrop-filter: blur(10px);
  margin-bottom: 60px;
}

.wishlist-section .section-header h2 i {
  color: #FFD700;
  filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.6));
}

/* =========================================================
   섹션 헤더
========================================================= */
.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 0;
  border-bottom: none;
  position: relative;
}

.section-header h2 {
  font-size: 1.4rem;
  margin: 0;
  font-weight: 700;
  color: #e5e5e5;
  display: flex;
  align-items: center;
  gap: 12px;
  letter-spacing: 0.5px;
  transition: color 0.3s ease;
}

.section-header h2:hover {
  color: #fff;
}

.section-header h2 i {
  font-size: 1.2rem;
  color: #e50914;
  filter: drop-shadow(0 0 8px rgba(229, 9, 20, 0.6));
}

.movie-count {
  padding: 6px 16px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  font-size: 0.9rem;
  color: #b3b3b3;
  font-weight: 500;
}

/* =========================================================
   영화 그리드 - 가로 슬라이더
========================================================= */
.movie-grid {
  display: flex;
  gap: 8px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  padding: 20px 0;
  
  /* 스크롤바 스타일 */
  scrollbar-width: thin;
  scrollbar-color: rgba(229, 9, 20, 0.5) rgba(0, 0, 0, 0.3);
}

/* 웹킷 브라우저용 스크롤바 */
.movie-grid::-webkit-scrollbar {
  height: 8px;
}

.movie-grid::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.movie-grid::-webkit-scrollbar-thumb {
  background: linear-gradient(90deg, #e50914, #b20710);
  border-radius: 10px;
}

.movie-grid::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(90deg, #f40612, #e50914);
}

/* 영화 카드 크기 */
.movie-grid .movie-card {
  flex: 0 0 150px;
  min-width: 150px;
  max-width: 150px;
  transition: all 0.3s ease;
}

/* 카드 호버 시 확대 효과 */
.movie-grid .movie-card:hover {
  transform: scale(1.08);
  z-index: 10;
}

/* =========================================================
   슬라이더 네비게이션 버튼
========================================================= */
.slider-nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 60px;
  height: 140px;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: white;
  font-size: 32px;
  cursor: pointer;
  z-index: 100;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
}

/* 섹션에 마우스 올릴 때 버튼 표시 */
.movie-section:hover .slider-nav-btn {
  opacity: 1;
  pointer-events: all;
}

/* 버튼에 마우스 올릴 때 */
.slider-nav-btn:hover {
  background: rgba(0, 0, 0, 0.8);
}

.slider-nav-btn.prev {
  left: 0;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}

.slider-nav-btn.prev:hover {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);
}

.slider-nav-btn.next {
  right: 0;
  background: linear-gradient(to left, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}

.slider-nav-btn.next:hover {
  background: linear-gradient(to left, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);
}

/* 화살표 아이콘 */
.slider-nav-btn i {
  color: white;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.8));
}

/* =========================================================
   반응형
========================================================= */
@media (max-width: 1200px) {
  .movie-section {
    padding: 0 40px;
  }

  .movie-grid .movie-card {
    flex: 0 0 140px;
    min-width: 140px;
    max-width: 140px;
  }
}

@media (max-width: 768px) {
  .movie-section {
    padding: 0 20px;
  }

  .wishlist-section {
    padding: 20px;
  }

  .section-header h2 {
    font-size: 1.2rem;
  }

  .movie-grid .movie-card {
    flex: 0 0 130px;
    min-width: 130px;
    max-width: 130px;
  }

  .movie-grid .movie-card:hover {
    transform: scale(1.05);
  }

  .slider-nav-btn {
    display: none;
  }

  .movie-count {
    font-size: 0.8rem;
    padding: 5px 12px;
  }
}

@media (max-width: 480px) {
  .movie-section {
    padding: 0 15px;
  }

  .wishlist-section {
    padding: 15px;
  }

  .section-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 15px;
  }

  .section-header h2 {
    font-size: 1.1rem;
  }

  .movie-grid {
    gap: 6px;
    padding: 15px 0;
  }

  .movie-grid .movie-card {
    flex: 0 0 110px;
    min-width: 110px;
    max-width: 110px;
  }

  .movie-grid .movie-card:hover {
    transform: scale(1.03);
  }
}
